.productDetails {
    display: flex;
    width: 100vw;
    max-width: 100%;
    background-color: white;
    height: auto;
    box-sizing: border-box;
    padding: 1vmax;
}

.productDetails > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* justify-content: center; */
    justify-items: center;
    /* align-items: center; */
    padding: 2vmax;
    box-sizing: border-box;
    border:  2px white solid;
    margin-left: 10vmax;
}

.productDetails > div:first-child  {
    width: 50vmax;
}

.productDetails > div:last-child {
    align-items: flex-start;
   
}

.detailsBlock-1 >h2 {
    color: rgb(54, 54, 54);
    font:600 2.5vmax "Roboto"
}

.detailsBlock-1 > p {
    color: rgba(54, 54, 54, 0.582);
    font: 200 0.6vmax cursive;
}

.detailsBlock-2 {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 70%;
    padding: 1vmax 0;
}

.detailsBlock-2-span {
    font: 200 1vmax cursive; 
    color: rgba(0, 0, 0, 0.699);
}

.detailsBlock-3 {
    width: 70%;
}

.detailsBlock-3 > h1 {
    color: rgba(17, 17, 17, 0.795);
    font: 400 1.8vmax "Franklin Gothic Medium";
    margin: 1vmax 0;
}

.detailsBlock-3-1 {
    display: flex;
    align-items: center;
}

.detailsBlock-3-1-1 > button {
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    padding: 0vmax;
    width: 2vmax;
    height: 2vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}

.detailsBlock-3-1-1 > button:hover {
    background-color: rgba(0, 0, 0, 0.767);
}

.detailsBlock-3-1-1 > input {
    border: none;
    padding: 0.5vmax;
    width: 2vmax;
    height: 2vmax;
    text-align: center;
    outline: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0, 0, 0, 0.74);
}

.detailsBlock-3-1 > button:last-child {
    border: none;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 1.3vmax "Roboto";
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
    background-color: rgb(214, 84, 61);
}

.detailsBlock-3 > p {
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    padding: 1vmax 0;
    color: rgba(0, 0, 0, 0.651);
    font: 400 1.7vmax "Roboto";
    margin: 1vmax 0;
}

.detailsBlock-4 {
    color: rgba(0, 0, 0, 0.897);
    font: 500 1.5vmax sans-serif;
}

.detailsBlock-4 > p {
    color: rgba(0, 0, 0, 0.534);
    font: 300 1vmax sans-serif;
}

.submitReview {
    border: none;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 1.3vmax "Roboto";
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.submitReview:hover {
    background-color: rgb(197, 68, 45);
    transform: scale(1.1);
}

.submitDialog {
    display: flex;
    flex-direction: column;
}

.submitDialogTextArea {
    border: 1px solid rgba(0, 0, 0, 0.082);
    margin: 1vmax 0;
    outline: none;
    padding: 1rem;
    font: 300 1vmax "Roboto";
}



.reviewHeading {
    color: black;
    font: 500 1.4vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.226);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    margin-bottom: 4vmax;
}

.reviews {
    display: flex;
    overflow: auto;
}

.reviewCard {
    flex: none;

    box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
    border: 1px solid rgba(56, 56, 56, 0.116);
    width: 30vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1vmax;
    padding: 3vmax;
}
.reviewCard > img {
    width: 5vmax;
}
.reviewCard > p {
    color: rgba(0, 0, 0, 0.836);
    font: 600 0.9vmax "Roboto";
}
.reviewCardComment {
    color: rgba(0, 0, 0, 0.445);
    font: 300 0.8vmax cursive;
}

.noReviews {
    font: 400 1.3vmax "Gill Sans";
    text-align: center;
    color: rgba(0, 0, 0, 0.548);
}



/* media query   for moblie   */


@media (max-width:768px){

    .productDetails {
        flex-direction: column;
        height: unset;
    }

    .productDetails > div:first-child {
        justify-content: center;
        margin-left: 20vmax;
    }
    .productDetails > div:last-child {
        align-items: center;
    }

    .detailsBlock-1 > h2 {
        font-size: 3.5vmax;
        text-align: center;
    }

    .detailsBlock-1 > p {
        text-align: center;
        font-size: 1.5vmax;
    }

    .detailsBlock-2 {
        justify-content: center;
    }
    .detailsBlock-2 > span {
        font-size: 2vmax;
    }

    .detailsBlock-3 > h1 {
        font: 700 4vmax "Franklin Gothic Medium";
        text-align: center;
    }

    .detailsBlock-3-1 {
        flex-direction: column;
    }

    .detailsBlock-3-1-1 {
        padding: 2vmax 0;
    }

    .detailsBlock-3-1-1 > button {
        padding: none;
        margin-top: none;
        width:  6vmax;
        height: 6vmax;
    }

    .detailsBlock-3-1-1 > input {
        padding: 1.5vmax;
        width: 6vmax;
        height: 6vmax;
        font: 400 1.8vmax "Roboto";
    }

    .detailsBlock-3-1 > button:last-child {
        font: 500 2.5vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }

    .detailsBlock-3 > p {
        padding: 2.5vmax 0;
         text-align: center;
         font: 400 3vmax "Roboto";
    }

    .detailsBlock-4 {
        font: 500 3.5vmax sans-serif;
    }

    .detailsBlock-4 > p {
        font: 300 2.5vmax sans-serif;
    }

    .submitReview {
        font: 500 2.2vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }

    .reviewCard > p {
        font:  600 3vw "Roboto";
    }

    .reviewCardComment {
        font:  300 5vw cursive;
    }


}